ndex) in defaultCategories\"\r\n                                    :key=\"index\"\r\n                                    :data-val=\"item.id\"\r\n                                    class=\"text-center txt\"\r\n                                    style=\"\"\r\n                                    @click=\"getProducts($event)\"\r\n                                    :href=\"'/services/vendor/' + item.path\"\r\n                                >\r\n                                    <div style=\"position: relative\">\r\n                                        <img\r\n                                            :src=\"\r\n                                                '/assets/defaults/vendors/images/' +\r\n                                                item.imageOne\r\n                                            \"\r\n                                            alt=\"\"\r\n                                            style=\"width:100%; height:100%, object-fit:cover; border-radius:20px\"\r\n                                        />\r\n                                        <span\r\n                                            style=\"\r\n                                                display: block;\r\n\r\n                                                background: white;\r\n                                                width: 100%;\r\n                                                border: 1px solid black;\r\n                                                border-radius: 20px;\r\n                                                padding: 10px;\r\n                                            \"\r\n                                            >{{ item.name }}</span\r\n                                        >\r\n                                    </div>\r\n                                </a>\r\n                            </div>\r\n                        </div>\r\n                        <div\r\n                            style=\"margin-top: 3em; display: none\"\r\n                            class=\"vendor-address-form-fields\"\r\n                        >\r\n                            <!-- <label style=\"display: none\" for=\"vendorCategories\">\r\n                                Select Services\r\n                                <select\r\n                                    name=\"vendorCategories\"\r\n                                    id=\"vendorCategories\"\r\n                                    v-model=\"selectedCat\"\r\n                                    @change=\"getProducts\"\r\n                                >\r\n                                    <option hidden disabled selected>\r\n                                        Select vendor’s category\r\n                                    </option>\r\n                                    <option\r\n                                        v-for=\"(\r\n                                            item, index\r\n                                        ) in defaultCategories\"\r\n                                        :key=\"index\"\r\n                                        :value=\"item.id\"\r\n                                    >\r\n                                        {{ item.name }}\r\n                                    </option>\r\n                                </select>\r\n                            </label> -->\r\n                            <div>\r\n                                <div\r\n                                    style=\"display: flex; width: 100%\"\r\n                                    v-if=\"catProducts != null\"\r\n                                >\r\n                                    <div style=\"width: 25%\">\r\n                                        <a\r\n                                            :href=\"\r\n                                                '/vendor/welcome/' +\r\n                                                vendorDetails.id +\r\n                                                '/' +\r\n                                                item.id\r\n                                            \"\r\n                                            v-for=\"(item, index) in catProducts\"\r\n                                            :key=\"index\"\r\n                                        >\r\n                                            <div>\r\n                                                {{ item.name }}\r\n                                            </div>\r\n                                        </a>\r\n                                    </div>\r\n                                    <div\r\n                                        style=\"\r\n                                            flex: 1;\r\n                                            display: flex;\r\n                                            justify-content: flex-end;\r\n                                        \"\r\n                                    >\r\n                                        <a :href=\"route\">\r\n                                            <div class=\"addCatProduct\">\r\n                                                Add\r\n                                                {{ selectedCatName }}\r\n                                            </div>\r\n                                        </a>\r\n                                    </div>\r\n                                </div>\r\n                                <div style=\"display: flex\" v-if=\"showDisp\">\r\n                                    No\r\n                                    {{\r\n                                        defaultCategories.find(\r\n                                            (x) => x.id === selectedCat\r\n                                        ).name\r\n                                    }}\r\n                                    added\r\n\r\n                                    <div>\r\n                                        <a :href=\"route\">\r\n                                            <div class=\"addCatProduct\">\r\n                                                Add\r\n                                                {{\r\n                                                    defaultCategories.find(\r\n                                                        (x) =>\r\n                                                            x.id === selectedCat\r\n                                                    ).name\r\n                                                }}\r\n                                            </div>\r\n                                        </a>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n                            <!-- <label for=\"vendorLocationLink\">\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"map\"\r\n                                    id=\"vendorLocationLink\"\r\n                                    placeholder=\"Add location link\"\r\n                                    v-model=\"sendProfile.map\"\r\n                                />\r\n                                <button id=\"location-link\">\r\n                                    <img\r\n                                        src=\"/assets/vendors/dashboard/location-icon.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                    Insert Link\r\n                                </button>\r\n                            </label> -->\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </form>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    props: [\"vendorId\"],\r\n    data() {\r\n        return {\r\n            route: null,\r\n            sendCategory: [],\r\n            defaultCategories: null,\r\n            selectedCat: null,\r\n            prodDisplay: false,\r\n            showDisp: false,\r\n            catProducts: null,\r\n            vendorDetails: null,\r\n            selectedCatName: null,\r\n            linkkk: null,\r\n        };\r\n    },\r\n    methods: {\r\n        getvendorprofile() {\r\n            var _this = this;\r\n            let v = this.vendorId;\r\n            axios\r\n                .get(`/api/vendor/vendorsCategory/${v}`)\r\n                .then(function (response) {\r\n                    _this.defaultCategories = response.data[1];\r\n                    _this.vendorDetails = response.data[0];\r\n                })\r\n                .catch((errors) => {\r\n                    console.log(\"Get All: \" + errors);\r\n                });\r\n        },\r\n    },\r\n    mounted() {\r\n        this.$nextTick(function () {\r\n            this.getvendorprofile();\r\n            console.log(this.vendorId);\r\n        });\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n/* Nav Tab CSS Start*/\r\n.txt {\r\n    width: 25%;\r\n\r\n    padding: 10px;\r\n    color: rgb(244, 63, 94);\r\n    font-weight: 900;\r\n}\r\n/* Nav Tab CSS Ends */\r\n.errMsg {\r\n    color: red;\r\n    font-size: 10px;\r\n}\r\n.addCatProduct {\r\n    background: #c4456f;\r\n    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.25);\r\n    border-radius: 22.4987px;\r\n    padding: 7px 30px;\r\n    font-family: Segoe UI;\r\n    font-style: normal;\r\n    font-weight: 600;\r\n    font-size: 11.2493px;\r\n    line-height: 15px;\r\n    text-transform: capitalize;\r\n    color: #ffffff;\r\n    outline: none;\r\n    border: 0;\r\n    width: max-content;\r\n    cursor: pointer;\r\n}\r\n.btn-container {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: center;\r\n    margin: 1em 0;\r\n}\r\n.tab-btn.active {\r\n    background: #461952;\r\n    color: white !important;\r\n    background: linear-gradient(0deg, #c4456f, #c4456f),\r\n        linear-gradient(0deg, #c4456f, #c4456f), #c4456f;\r\n    bord